<template>
    <div class="container">
       <div class="main__cars" v-if="posts">
        <div class="main__cars-item" v-for="post in posts" :key="post.id">
            <p>ID: {{ post.id }}</p>
            <p>Title: {{ post.title }}</p>
            <p>Body: {{ post.body }}</p>
        </div>
       </div>
       <div v-else>
        Идет загрузка...
       </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getPosts()
        },
        methods: {
            getPosts() {
                this.$store.dispatch('getPosts')
            }
        },
        computed:{
            posts(){
                return this.$store.state.posts
            }
        }
    }
</script>